.offers {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding-top: 102px;
  background-color: #eab734;
  padding-bottom: 100px;
}


.offers__container {
  display: flex;
  width: 100%;
  max-width: 1098px;
  justify-content: space-between;
}

.offers__btn {
  width: 215px;
  height: 65px;
  background-color: black;
  border-radius: 30px;
  color: white;
  margin-top: 15px;
  position: relative;
  left: -42px;
  transition: 1s;

  &:hover {
    transform: scale(1.1);
  }
}

.offers__info p {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  color: black;
  padding-bottom: 27px;
  position: relative;

  &:last-child {
    padding-bottom: 42px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 30px;
    height: 30px;
    background-color: black;
    border-radius: 50%;
    left: -43px;
    top: -5px;
  }
}


.offers h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  padding-bottom: 78px;
}

.offers__imgs {
  width: 560px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.offers__imgs img {
  margin-bottom: 33px;
  border-radius: 5px;
}

@media (max-width: 1250px) {
  .offers__imgs {
    width: 100%;
    max-width: 264px;
  }

  .offers__container {
    max-width: 700px;
  }
}

@media (max-width: $mobile-width) {
  .offers__container {
    flex-direction: column;
    align-items: center;
  }

  .offers__info p {
    width: 100%;
    text-align: center;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    margin: 0;
    padding: 0 48px;
    padding-bottom: 20px;
    padding-top: 16px;

    &::before {
      display: none;
    }
  }

  .offers__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 21px;
  }

  .offers__imgs {
    max-width: 270px;

    img {
      width: 125px;
      height: 93px;
    }
  }

  .offers h2 {
    text-align: center;
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    line-height: 24px;
    max-width: 220px;
    padding-bottom: 30px;
  }

  .offers {
    padding-bottom: 17px;
    padding-top: 45px;
  }

  .offers__btn {
    left: 0;
  }
}
