.service {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 120px;
}

.service__container {
  width: 100%;
  max-width: $container;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.service__item {
  width: 264px;
  height: 134px;
  border: 1px solid #CECDCD;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-bottom: 34px;
}

.service__item h3 {
  font-family: $fontfam;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  max-width: 214px;
}


.service__popup-block {
  display: none;
  width: 264px;
  height: 134px;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  top: 0;
  background: url("../images/content/bg-service.jpg") no-repeat;
  background-size: 264px 134px;
  padding: 5px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
}

.service__item:hover .service__popup-block {
  display: flex;
}


.service__link {
  width: 156px;
  height: 39px;
  color: black;
  border-radius: 50px;
  background-color: #eab734;
  display: flex;
  justify-content: center;
  align-items: center;

  &:hover {
    background-color: #f06543;
  }
}


.service h2 {
  padding-top: 101px;
  padding-bottom: 61px;
  font-family: $fontfam;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
}

.service__button {
  background: none;
  border: 1px solid #101010;
  width: 156px;
  height: 39px;
  border-radius: 50px;

  &:hover {
    background-color: #eab734;
  }
}

.item-block.active {
  display: flex;
}

.spoiler-open-two {
  display: none;
}

.spoiler-open-two.disbled-sp {
  display: inline-block;
}

//.item-block[style*="display: block"]{
//  display:flex!important;
//}
.item-block__wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: $container;
  justify-content: space-between;
}

.item-block {
  display: none;
  overflow: hidden;
}


.spoiler-close {
  cursor: pointer;
  display: none;
}

@media (max-width: 1300px) {
  .service__container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (max-width: $tablet-width) {
  .service h2 {
    text-align: center;
  }

  .service__container {
    padding: 0 20px;
    flex-wrap: wrap;
    justify-content: space-around;
  }
}

@media (max-width: $mobile-width) {
  .service h2 {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    max-width: 234px;
    padding-top: 30px;
    padding-bottom: 22px;
  }

  .service__item {
    width: 264px;
    height: 56px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .service__item-block h3 {
    margin: 0 auto;
    padding-top: 15px;
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
  }

  .service__popup-block {
    background: none;
    width: 264px;
    height: 54px;
  }

  .service__link {
    width: 264px;
    height: 54px;
    padding-top: 10px;
    padding-bottom: 10px;
    display: none;
  }

  .service__container {
    width: 294px;
    flex-direction: column;
    padding-left: 0;
    padding-right: 0;
  }

  .service__item-block {
    width: 264px;
    height: 54px;
    margin: 0 auto;
  }

  .service__button {
    display: none;
  }

  .service {
    padding-bottom: 50px;
  }
}
