.quality__item {
  width: 558px;
  height: 199px;
  background-color: white;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 100;
  margin-bottom: 27px;
}

.quality__container {
  background-color: #eab734;
  display: flex;
  justify-content: space-between;
  width: 1150px;
  flex-wrap: wrap;
}

.quality {
  background-color: #eab734;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 80px;
}

.quality__item p {
  color: black;
  max-width: 353px;
}


.quality__item h4 {
  position: absolute;
  top: -2px;
  left: 50px;
  font-weight: 900;
  font-size: 170px;
  line-height: 199px;
  z-index: 0;
  opacity: 0.4;
  color: #eab734;
}

.quality__item h3 {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  padding-bottom: 14px;
}


.quality h2 {
  font-family: $fontfam;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  /* identical to box height */
  display: flex;
  justify-content: center;
  text-align: center;
  padding-top: 92px;
  padding-bottom: 35px;
}

@media (max-width: 1250px) {
  .quality__item {
    width: 364px;
    height: 258px;
    border-radius: 20px;
  }

  .quality__container {
    max-width: 800px;
  }
}

@media (max-width: $tablet-width) {
  .quality__container {
    flex-wrap: wrap;
    width: 768px;
    justify-content: space-around;
  }

  .quality__item-block {
    padding-left: 12px;
  }
}

@media (max-width: $mobile-width) {
  .quality__item {
    width: 264px;
    height: 258px;
    border-radius: 20px;
  }

  .quality__container {
    max-width: 280px;
    border-radius: 20px;
    justify-content: space-between;
    flex-wrap: nowrap;
  }

  .quality__item-block {
    display: flex;
    flex-direction: column;
    padding-top: 52px;
    padding-left: 28px;
  }

  .quality__item p {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
  }

  .quality__item h3 {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    max-width: 171px;
  }

  .quality__item h4 {
    left: 29px;
  }

  .quality h2 {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    max-width: 234px;
    padding-top: 43px;
  }
}
