.map {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 200px;
}

.map__select-block {
  display: flex;
  flex-direction: column;
}

.map__form input {
  width: 460px;
  border-radius: 30px;
  height: 65px;
  display: flex;
  flex-direction: column;
  padding-left: 27px;
  justify-content: center;
  margin-bottom: 14px;
  border: 1px solid #EAB734;
  color: black;

  &::placeholder {
    color: black;
  }
}

.map__select {
  width: 460px;
  border-radius: 30px;
  height: 65px;
  display: flex;
  flex-direction: column;
  padding-left: 27px;
  justify-content: center;
  position: relative;
  background: white;
  border: 1px solid #EAB734;
  margin-bottom: 14px;

  &::before {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: #EAB734;
    transform: rotate(45deg);
    right: 47px;
    transition: 0.3s ease;
  }

  &::after {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: #EAB734;
    transform: rotate(-45deg);
    right: 40px;
    transition: 0.3s ease;
  }

  &.is-open {
    .select-body {
      display: block;
    }
  }

  &-header {
    cursor: pointer;
  }

  &-body {
    display: none;
    justify-content: flex-start;
    padding-left: 27px;
    left: -1px;
    position: absolute;
    top: 100%;
    right: 0;
    border: 1px solid #eab734;
    border-radius: 0 0 25px 25px;
    background: white;
    z-index: 1000;
    align-items: center;
  }

  &-item {
    cursor: pointer;
    display: flex;
    width: 20px;
    height: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 12px;
    border-radius: 5px;
    margin-bottom: 10px;
    border: 1px solid #EAB734;
    margin-right: 10px;
    font-family: $fontfam;
    font-style: normal;
    font-weight: 300;
    font-size: 15px;
    line-height: 18px;
    /* identical to box height */

    text-align: center;

    &:hover {
      background-color: #EAB734;
    }

    &--class {
      width: auto;
      height: auto;
      border: none;
      padding: 20px;
      text-align: center;
      padding-left: 5px;
      padding-right: 5px;
    }

    &--bag {
      padding: 20px;
    }
  }
}

.select.is-active .map__select-body {
  display: flex;
  border-top: 0;
  width: 460px;
}

.map__select.is-active {
  border-top: 1px solid #eab734;
  border-left: 1px solid #EAB734;
  border-right: 1px solid #EAB734;
  border-radius: 25px 25px 0 0;

  &::before {
    transform: rotate(-45deg);
  }

  &::after {
    transform: rotate(45deg);
  }
}

.map__imd-block {
  width: 656px;
  height: 697px;
  background: url("../images/content/map.jpg") no-repeat;
}

.map__container {
  display: flex;
  width: 100%;
  max-width: $container;
  justify-content: space-between;
}

.map__sub-btn {
  width: 460px;
  height: 65px;
  background-color: #eab734;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: black;
}

.map p {
  font-family: $fontfam;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  text-align: center;
  color: black;
  max-width: 602px;
  padding-bottom: 74px;
}

.map h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  text-align: center;
}

.map__block-mob {
  display: none;
}


@media (max-width: 1250px) {
  .map__imd-block {
    width: 500px;
    margin-right: 20px;
  }

  .map__select-block {
    margin-left: 20px;
  }
}

.map__control-nav {
  padding: 10px;
  background-color: #eab734;
  width: 284px;
  position: absolute;
  top: 148px;
  z-index: 99;
  border-radius: 30px;
  display: none;

  &--second {
    left: 66%;
  }
  span {
    background-color: #EAB734;
  }

  input {
    border-radius: 20px;
  }
}

.map__controls {
  background-color: #EAB734;
  color: black;
  border-radius: 30px;
  text-align: center;
}


@media (max-width: $tablet-width) {
  .map__imd-block {
    display: none;
  }

  .map__container {
    display: flex;
    justify-content: center;
  }

  .map {
    padding-bottom: 51px;
  }
}

@media (max-width: $mobile-width) {
  .map__select-block {
    display: none;

    &::before {
      content: "";
      background: black;
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      opacity: 0.9;
      height: 100%;
      z-index: 9999;
    }
  }


  .map__controls {
    display: none;
  }

  .map__form {
    position: fixed;
    top: 26px;
    left: 0;
    width: 100%;
    height: 100%;
    background: black;
    outline: none;
    border-radius: 4px;
    z-index: 99999;
    opacity: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
  }


  .form__close {
    position: absolute;
    top: 24px;
    right: 94px;
    padding: 10px;

    &::before {
      content: "";
      position: absolute;
      width: 30px;
      height: 4px;
      transform: rotate(45deg);
      background: #EAB734;
      top: 0;
      right: 0;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 30px;
      height: 4px;
      transform: rotate(-45deg);
      background: #EAB734;
    }
  }

  .map__block-mob {
    display: flex;
  }

  .map__btn-open {
    width: 264px;
    height: 65px;
    background-color: #eab734;
    border-radius: 30px;
  }

  .map h2 {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    max-width: 234px;
    padding-bottom: 14px;
  }

  .map p {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    max-width: 234px;
    padding-bottom: 28px;
  }

  .map__imd-block {
    width: 264px;
    height: 219px;
    margin-top: 45px;
    border-radius: 5px;
    display: flex;
    margin-right: 0;
  }

  .map__container {
    flex-direction: column;
    display: flex;
    align-items: center;
  }

  .map {
    padding-bottom: 51px;
  }

  .map__form input {
    width: 265px;
    height: 65px;
    margin-bottom: 18px;
  }

  .map__select {
    width: 265px;
    height: 65px;
    margin-bottom: 18px;

    &::before {
      display: none;
    }

    &::after {
      display: none;
    }
  }

  #phone {
    width: 265px;
    height: 65px;
  }

  .select.is-active .map__select-body {
    width: 266px;
    flex-wrap: wrap;
  }

  .slect-disbled {
    display: none;
  }
}
