.footer {
  display: flex;
  justify-content: center;
  background-color: black;
}

.footer__container {
  width: 100%;
  max-width: $container;
  justify-content: space-between;
  display: flex;
  padding-top: 108px;
  padding-bottom: 168px;
}

.footer__col {
  display: flex;
  flex-direction: column;
}


.footer__main-block h3 {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;
  color: #eab734;
}


.footer__main-block p {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  opacity: 0.6;
  max-width: 206px;
  padding-bottom: 27px;
}


.footer__options h4 {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  color: #eab734;
  padding-bottom: 13px;
}

.footer__options li {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  padding-bottom: 13px;
  opacity: 0.6;
}


.footer__service-block h4 {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  color: #eab734;
  padding-bottom: 8px;
}

.footer__service-block li {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  padding-bottom: 8px;
  opacity: 0.6;
}

.footer__contacts-tels {
  display: flex;
  flex-direction: column;
  padding-right: 21px;
}

.footer__contacts svg {
  width: 30px;
  height: 30px;
  fill: #EAB734;
  margin-right: 16px;
}

.footer__contacts {
  display: flex;
}

.footer__form-container {
  display: flex;
  flex-direction: column;
}

.footer__form {
  display: flex;
  flex-direction: column;
}

.footer__form-container h3 {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;
  color: #eab734;
  padding-top: 40px;
  padding-bottom: 20px;
}

.footer__form input {
  margin-bottom: 9px;
  width: 264px;
  height: 45px;
  border-radius: 20px;
  padding-left: 27px;

  &::placeholder {
    font-family: $fontfamali;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    color: #9C9C9C;
  }
}

.footer__form-btn {
  width: 264px;
  height: 45px;
  border-radius: 25px;
  background-color: #eab734;
}

.footer__btn {
  display: none;
}

@media (max-width: 1200px) {
  .footer__container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (max-width: $tablet-width) {
  .footer__service-block {
    display: none;
  }

  .footer__container {
    justify-content: space-around;
  }

  .footer__rating {
    display: none;
  }
}

@media (max-width: $mobile-width) {
  .footer__container {
    flex-direction: column;
    align-items: center;
    padding-top: 45px;
    padding-bottom: 50px;
  }

  .footer__form-wrapper {
    display: none;
  }

  .footer__btn {
    display: flex;
    width: 264px;
    height: 41px;
    background-color: #eab734;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 30px;
    margin-top: 27px;
  }

  .footer__options {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .footer__options li {
    text-align: center;
  }

  .footer__main-block p {
    text-align: center;
  }

  .footer__main-block h3 {
    text-align: center;
  }

  .footer__contacts-tels {
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    text-align: right;
  }

  .footer__contacts {
    display: flex;
    justify-content: center;
  }

  .footer__contacts svg {
    &:last-child {
      margin-right: 0;
      margin-left: 17px;
    }
  }
}
