.contacts {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.contacts__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: $container;
  padding-bottom: 105px;
}

.contacts__form-block {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contacts__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 40px;
}

.contacts__link-mob svg {
  left: -160px;
  position: relative;
}

.contacts__link-mob .icon-whats {
  width: 30px;
  height: 30px;
  fill: #4CAF50;
}

.contacts__link-mob .icon-viber {
  width: 30px;
  height: 30px;
  fill: #6F3FAA;
}

.icon-viber path {
  fill: white;
}

.contacts__link-place {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  /* identical to box height */
  width: 100%;
  color: black;
  padding-bottom: 20px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    top: 50px;
    left: -33px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  }

  svg {
    width: 30px;
    height: 30px;
    fill: #EAB734;
    position: relative;
    left: -336px;
  }
}

.contacts__link-mob .icon-phone {
  width: 30px;
  height: 30px;
  fill: #EAB734;
}

.contacts__link-mob {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  /* identical to box height */
  color: black;
  position: relative;
  padding-bottom: 18px;
  padding-top: 18px;
}

.contacts__form input {
  width: 362px;
  height: 65px;
  border-radius: 30px;
  border: 1px solid #eab734;
  padding-left: 27px;
  margin-bottom: 16px;
}

.contacts__form textarea {
  width: 362px;
  height: 135px;
  border-radius: 30px;
  border: 1px solid #eab734;
  margin-bottom: 19px;
  padding-left: 27px;
  padding-top: 10px;
  outline: none;
}

.contacts__links-block {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
}

.contacts__img-block {
  width: 754px;
  height: 644px;
  background: url("../images/content/map-cont.jpg") no-repeat;
}

.contacts h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  /* identical to box height */

  text-align: center;
  padding-bottom: 60px;
  padding-top: 52px;
}


@media (max-width: 1200px) {
  .contacts__img-block {
    width: 600px;
    height: 600px;
  }

  .contacts__container {
    max-width: 1000px;
  }
}

@media (max-width: $tablet-width) {
  .contacts__img-block {
    display: none;
  }

  .contacts__container {
    align-items: center;
    justify-content: center;
  }

  .contacts__links-block {
    align-items: center;
  }
}


@media (max-width: $mobile-width) {
  .contacts__form input {
    width: 264px;
    height: 65px;
  }

  .contacts__form textarea {
    width: 264px;
    height: 113px;
  }

  .contacts__link-mob {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    /* identical to box height */
    padding-left: 40px;
    text-align: center;

    &:first-child {
      padding-top: 0;
    }
  }

  .contacts__link-place {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    max-width: 178px;
    position: relative;
    padding-bottom: 0;

    svg {
      left: -111px;
      top: -35px;
    }

    &::before {
      width: 120%;
    }
  }

  .contacts__links-block {
    padding-left: 0;
  }

  .contacts__img-block {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 277px;
    margin-top: 31px;
    background: url("../images/content/map-cont-mob.jpg") no-repeat;
    background-position: center;
    border-radius: 0;
  }

  .contacts__container {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-bottom: 0;
  }

  .contacts h2 {
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    line-height: 24px;
    padding-top: 48px;
    padding-bottom: 35px;

    text-align: center;
  }
}
