.servform__form input {
  border: 1px solid #eab734;
  border-radius: 30px;
  width: 263px;
  height: 65px;
  padding: 15px;
  padding-left: 27px;

  &::placeholder {
    color: #7E7E7E;
  }
}

.servform__btn {
  width: 361px;
  border-radius: 50px;
  background: #eab734;
  color: black;
  padding: 15px;

  &:hover {
    background-color: orange;
  }
}

.servform__form {
  display: flex;
  width: 100%;
  max-width: 1165px;
  justify-content: space-between;
}

.servform__form-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.servform__form small {
  max-width: 340px;
  text-align: center;
  font-weight: 300;
  font-size: 13px;
  line-height: 16px;
  padding-top: 5px;
}


.servform {
  display: flex;
  justify-content: center;
  padding-bottom: 60px;
}

@media (max-width: 1250px) {
  .servform__form {
    justify-content: center;
  }

  #phone {
    width: 264px;
  }
}

@media (max-width: $tablet-width) {
  .servform__form {
    flex-direction: column;
    align-items: center;
  }

  .servform__form input {
    width: 100%;
    max-width: 660px;
    margin-bottom: 20px;
  }

  #phone {
    width: 100%;
    max-width: 660px;
  }
}


@media (max-width: $mobile-width) {
  .servform__form {
    display: none;
  }

  .servform {
    padding-bottom: 0;
  }
}
