.slider__item {
  border-radius: 25px;
  background-color: #eab734;
  width: 558px;
  height: 378px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 45px;
}

.slider__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: $container;
  margin: 0 -15px;
}

.slider__item h3 {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  padding-bottom: 10px;
}

.slider__item h4 {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  padding-bottom: 30px;
}

.slider__item p {
  font-family: $fontfamM;
  font-style: italic;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: black;
  margin: 0 20px;
}

.slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 100px;
}

.slider h2 {
  font-family: $fontfam;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  /* identical to box height */
  padding-bottom: 50px;
  text-align: center;
}

.slider__item img {
  display: flex;
  border-radius: 50%;
  margin: 0 auto;
  margin-top: 28px;
  margin-bottom: 20px;
}


@media (max-width: 1250px) {
  .slider__container {
    max-width: 900px;
  }

  .slider__item p {
    font-size: 16px;
  }
}

@media (max-width: $mobile-width) {
  .slider__item p {
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    margin: 0 auto;
  }

  .slider__item {
    width: 320px;
    height: 222px;
  }

  .slider__item img {
    width: 40px;
    height: 40px;
    margin-bottom: 12px;
    margin-top: 16px;
  }

  .slider__item h3 {
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
  }

  .slider__item h4 {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    padding-bottom: 12px;
  }

  .slick-slide {
    padding: 0;
  }


  .slider__dots li {
    display: none;
  }

  .slider__dots li button {
    height: inherit;
    width: inherit;
    outline: none;
    border: none;
    cursor: pointer;
    background-color: transparent;
  }

  .slider__dots li.slick-active {
    display: inline-block;
  }

  .slider__dots li.before,
  .slider__dots li.after {
    display: inline-block;
  }

  .slider {
    padding-bottom: 30px;

    h2 {
      font-style: normal;
      font-weight: bold;
      font-size: 20px;
      line-height: 24px;
      text-align: center;
      max-width: 171px;
      padding-bottom: 25px;
    }
  }
}
