.advantages__item svg {
  width: 80px;
  height: 80px;
  fill: #eab734;
}

.advantages h2 {
  color: white;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  text-align: center;
  padding-top: 92px;
  padding-bottom: 61px;
}

.advantages__item p {
  font-family: $fontfam;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  color: black;
  max-width: 288px;
  padding-top: 34px;
}

.advantages__item h3 {
  padding-top: 36px;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
}

.advantages__item {
  width: 362px;
  height: 359px;
  background: white;
  position: relative;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 36px;
}

.advantages {
  background: black;
}

.advantages__container {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  //margin-left: 227px;
  //margin-right: 225px;
  justify-content: space-around;
  max-width: 1200px;
}

@media (max-width: $mobile-width) {
  .advantages__item {
     width: 264px;
     height: 251px;
     padding: 0 10px;
  }

  .advantages {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .advantages__container {
    max-width: 768px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-bottom: 50px;
  }

  .advantages__item h3 {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    padding-top: 12px;
  }

  .advantages__item p {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    max-width: 202px;
    padding-top: 18px;
  }

  .advantages__item-block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 27px;
    padding-top: 20px;
    //justify-content: center;
  }

  .advantages__item svg {
    width: 34px;
    height: 34px;
  }
}
