html {
  font-family: $fontfam;
  font-size: 16px;
  height: 100%;
}

body {
  height: 100%;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul,
a {
  list-style: none;
}

a {
  text-decoration: none;
  color: #ffffff;
  cursor: pointer;
}

a:visited {
  text-decoration: none;
}

p {
  color: #ffffff;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
  font-family: $fontfam;
}

input::-ms-clear {
  display: none;
}

input:invalid {
  box-shadow: none;
}

button,
input {
  border: none;
}

audio,
canvas,
iframe,
img,
svg,
video,
textarea {
  vertical-align: middle;
}

textarea {
  resize: none;
}

section {
  position: relative;
}

input,
select,
button {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}


input:not(:placeholder-shown):invalid {
  border: 1px solid red;
}

#map {
  width: 656px;
  height: 697px;
}

.disabled {
  display: none;
}


#map__about {
  width: 754px;
  height: 644px;
}

#date {
  position: relative;
}

.map__date {
  position: relative;
}


.xdsoft_datepicker.active .xdsoft_timepicker {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 208px;
    height: 253px;
    border-radius: 30px;
  }
}


.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_time_box {
  height: 253px;
  width: 208px;
  overflow: hidden;
  border-radius: 30px;
}

.xdsoft_datetimepicker .xdsoft_datepicker.active+.xdsoft_timepicker {
  margin-top: 8px;
  margin-bottom: 3px;
  position: absolute;
  top: 220px;
  left: 80px;
}

.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_time_box>div>div {
  font-family: $fontfam;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  position: relative;
  z-index: 100;
  padding-top: 5px;
}

.xdsoft_datetimepicker .xdsoft_datepicker {
  width: 347px;
  height: 210px;
}

.xdsoft_datetimepicker .xdsoft_month {
  width: 150px;
  position: relative;
  left: 65px;
}

.xdsoft_datetimepicker .xdsoft_calendar td>div {
  padding-right: 5px;
  text-align: center;
}


.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_time_box >div >div:hover {
  position: relative;
  z-index: 1000;
  color: #eab734;
  background: url("../images/content/bg-time.svg") no-repeat;
  background-position: center;
  background-color: #eab734;
  background-position-y: 5px;
}

.xdsoft_datetimepicker .xdsoft_calendar td:hover {
  padding-left: 5px;
  background: url("../images/content/bg-date.svg") no-repeat;
  color: #eab734;
  background-position: center;
  background-color: #EAB734;
}

.xdsoft_next {
  position: relative;
  left: -70px;
  z-index: 1000;

  &::before {
    content: "";
    position: absolute;
    width: 9px;
    top: 16px;
    left: 2px;
    height: 1px;
    background-color: black;
    transform: rotate(-45deg);
  }

  &::after {
    content: "";
    position: absolute;
    width: 9px;
    height: 1px;
    left: 2px;
    top: 9px;
    background-color: black;
    transform: rotate(45deg);
  }
}

.xdsoft_prev {
  position: relative;
  right: -70px;
  z-index: 1000;

  &::before {
    content: "";
    position: absolute;
    width: 9px;
    top: 16px;
    left: 2px;
    height: 1px;
    background-color: black;
    transform: rotate(45deg);
  }

  &::after {
    content: "";
    position: absolute;
    width: 9px;
    height: 1px;
    left: 2px;
    top: 9px;
    background-color: black;
    transform: rotate(-45deg);
  }
}

.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_today {
  color: #000;

  &:hover {
    color: #eab734;
  }
}

.xdsoft_timepicker.active .xdsoft_prev {
  display: none;
}

.xdsoft_timepicker.active .xdsoft_next {
  display: none;
}


#datepicker {
  position: relative;
  left: -20px;
}

@media (max-width: 1300px) {
  #map {
    width: 500px;
    height: 697px;
  }
}

@media (max-width: $mobile-width) {
  #map {
    width: 264px;
    height: 219px;
  }
}
