.transfer__info-block p {
  color: black;
  position: relative;
}

.transfer__info-block h2 {
  font-style: normal;
  font-weight: 900;
  font-size: 35px;
  line-height: 42px;
  padding-bottom: 57px;
}

.transfer__info-item {
  display: flex;
  align-items: center;
  margin-bottom: 39px;
}

.transfer__info-item svg {
  width: 41px;
  height: 42px;
  margin-right: 42px;
  fill: #eab734;
}

.transfer__container {
  max-width: 1165px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}


.transfer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 58px;
}

.transfer__img-block {
  width: 553px;
  height: 309px;
  position: relative;
  z-index: 100;
  margin-top: 52px;

  &::before {
    content: "";
    position: absolute;
    background-color: #eab734;
    width: 350px;
    height: 350px;
    right: 0;
    top: -50px;
    border-radius: 50%;
  }
}

.transfer__img-block img {
  position: relative;
}

.transfer__info-block {
  padding-bottom: 60px;
}

@media (max-width: 1250px) {
  .transfer__container {
    max-width: 1000px;
  }

  .transfer__info-block {
    padding-left: 20px;
  }
}

@media (max-width: $tablet-width) {
  .transfer__img-block {
    display: none;
  }

  .transfer__container {
    justify-content: center;
  }
}


@media (max-width: $mobile-width) {
  .transfer__info-item {
    flex-direction: column;
    align-items: center;

    svg {
      margin-right: 0;
      margin-bottom: 29px;
    }
  }

  .transfer__info-block {
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    padding-bottom: 0;
    padding-left: 0;
  }
}
