$color_link: #f06543;
$color_background: #e8e9eb;
$color_dark: #313638;


header {
  background: black;
  padding-bottom: 120px;
}

.navigation {
  display: flex;
  align-items: center;
  margin-left: 20px;
  font-size: 15px;
  background-color: black;
  font-family: $fontfamali;

  &__list {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }

  &__wrapper {
    width: 100%;
    background-color: black;
  }


  &__element {
    width: 26%;
    list-style: none;
    position: relative;

    &:hover::before {
      content: "";
      position: absolute;
      width: 68%;
      height: 5px;
      top: 42px;
      background: #EAB734;
      border-radius: 40px 40px 0 0;
    }
  }

  &__link {
    display: block;
    margin-right: 50px;
    text-decoration: none;
    color: white;
    box-shadow: inset 0 0 0 0 rgba($color_link, 0);
    transition: box-shadow 0.3s linear, color 0.3s linear;

    &:last-child {
      margin-right: 0;
    }

    &--service {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        width: 5px;
        height: 1px;
        background: #EAB734;
        left: 50px;
        top: 10px;
        transform: rotate(45deg);
        transition: 0.3s ease;
      }

      &::after {
        content: "";
        position: absolute;
        width: 5px;
        height: 1px;
        background: #EAB734;
        left: 53px;
        top: 10px;
        transform: rotate(-45deg);
        transition: 0.3s ease;
      }
    }
  }

  &__submenu {
    display: flex;
    justify-content: space-between;
    border: 1px solid #EAB734;
    background: black;
    margin-top: 25px;
    position: absolute;
    top: 100%;
    left: 0;
    opacity: 0;
    visibility: visible;
    transform: translate3d(0, 30px, 0);
    transition: .5s ease-out;
    transform-origin: 0% 0%;
    z-index: 9999;

    //&::before {
    //  content: "";
    //  position: absolute;
    //  width: 100%;
    //  height: 100%;
    //  background: black;
    //  border: 1px solid #EAB734;
    //}

    &-element {
      position: relative;
      list-style: none;
      z-index: 1000;
      background-color: black;
      display: inline-block;

      li {
        width: 300px;
      }

      //&:hover::after {
      //  content: "";
      //  position: absolute;
      //  width: 100%;
      //  height: 100%;
      //  background-color: #eab734;
      //  z-index: 0;
      //}
    }

    &-link {
      display: inline-block;
      text-align: left;
      text-decoration: none;
      color: $color_background;
      padding: 5px;
      width: auto;
      z-index: 2;

      &:hover {
        border-radius: 0 50px 50px 0;
        background: #EAB734;
      }
    }

    //&:hover {
    //  background-color: red;
    //}
  }

  &__element {
    &:hover {
      //box-shadow: inset 0 -50px 0 0 rgba($color_link, 1);
      //color: $color_background;

      .navigation__link {
        color: #EAB734;

        &--service {
          &::before {
            width: 5px;
            height: 1px;
            top: 10px;
            transform: rotate(-45deg);
          }

          &::after {
            width: 5px;
            height: 1px;
            transform: rotate(45deg);
          }
        }
      }
    }
  }
}


.navigation__element:nth-child(3):hover {
  .navigation__submenu {
    opacity: 1;
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1200px;
  padding: 10px;
  margin-top: 40px;
  border: 1px solid #eab734;
  border-radius: 50px;
  padding-bottom: 8px;
  padding-top: 8px;
}

.header__tel-container {
  display: flex;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 20px;
  background: #EAB734;
  border-radius: 50px;
  margin-right: 20px;
}

.header__tel-container svg {
  width: 20px;
  height: 20px;
  margin-right: 15px;
  margin-left: 30px;
}

.header__tel-wrapper {
  display: flex;
  flex-direction: column;
}

.header__logo-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-content: center;
  margin-right: 80px;
}

.header__logo-block h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;
  color: #eab734;
}

.header__tel-img {
  width: 20px;
  height: 20px;
}

.header__tel-link {
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  text-align: right;
  color: black;
}

.header__burger {
  display: none;
}

@media (max-width: 1300px) {
  .header__container {
    margin-left: 20px;
    margin-right: 20px;
  }

  .navigation__submenu {
    left: -143px;
  }
}

@media (max-width: $tablet-width) {
  body.lock {
    overflow: hidden;
  }

  .header__burger {
    display: block;
    position: relative;
    width: 50px;
    height: 50px;
    z-index: 3;
    background: black;
    margin-left: 24px;
  }

  .header__burger.active {
    background: none;
  }

  .header__burger span {
    position: absolute;
    background-color: #eab734;
    left: 15px;
    width: 30px;
    height: 5px;
    top: 22px;
    transition: all 0.3s ease 0s;
    margin-top: 2px;
    border-radius: 25px;
  }

  .header__burger::before,
  .header__burger::after {
    content: "";
    background-color: #eab734;
    position: absolute;
    width: 30px;
    height: 5px;
    left: 15px;
    transition: all 0.3s ease 0s;
    border-radius: 25px;
  }

  .header__burger::before {
    top: 15px;
  }

  .header__burger::after {
    bottom: 12px;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__burger.active::before {
    transform: rotate(45deg);
    top: 23px;
    width: 30px;
    background-color: #EAB734;
  }

  .header__burger.active::after {
    transform: rotate(-45deg);
    bottom: 22px;
    width: 31px;
    background-color: #EAB734;
  }

  .navigation {
    position: absolute;
    top: -115%;
    left: 56px;
    width: 350px;
    overflow: scroll;
    z-index: 1000;
    border: 1px solid #eab734;
    padding-left: 15px;

    &__link {
      //&--main {
      //  display: none;
      //}
    }

    &__element {
      width: 100%;
    }

    &__submenu {
      overflow: auto;
      padding-left: 20px;
      background: black;
      //margin-top: 50px;
      max-height: none;
      margin-top: 5px;
      border: 0;
      left: 0;
      flex-direction: column;
      opacity: 1;
      visibility: visible;
      transform: translate3d(0,0,0);

      &::before {
        display: none;
      }
    }

    &__element {
      padding-bottom: 20px;
      overflow: hidden;

      &:nth-child(3):hover {
        .navigation__submenu {
          position: relative;
          opacity: 1;
          visibility: visible;
          transform: translate3d(0,0,0);
        }
      }

      &::before {
        display: none;
      }
    }
  }

  .navigation__list {
    display: flex;
    flex-direction: column;
  }

  .navigation.active {
    top: 113px;
  }

  .navigation__link--service {
    &::before {
      display: none;
    }

    &::after {
      display: none;
    }
  }

  .header__logo-block {
    margin-right: 0;
  }

  .header__tel-container {
    flex-direction: row-reverse;
  }

  .header__tel-container svg {
    margin-right: 0;
    margin-left: 18px;
  }

  .header__tel-wrapper {
    padding-left: 15px;
    display: none;
  }

  .header__tel-wrapper.active {
    display: flex;
  }

  .header__logo-block h1 {
    font-size: 20px;
    line-height: 23px;
  }

  .header__tel-link {
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
  }
}

@media (max-width: $mobile-width) {
  .header__logo-block.active {
    display: none;
  }

  .navigation.active {
    top: 74px;
    padding-top: 10px;
  }

  .navigation {
    left: 14px;
    top: -170%;
    width: 286px;

    &__submenu-link {
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 14px;
    }
  }

  .header__container {
    border: none;
    margin-top: 0;
  }

  .header__tel-container {
    align-items: center;
    padding-top: 7px;
    padding-bottom: 10px;
    padding-left: 4px;
    padding-right: 3px;
    background: #eab734;
    margin-right: 0;
    margin-top: 5px;
  }

  .header__tel-container svg {
    margin-right: 6px;
    margin-left: 5px;
    margin-top: 2px;
  }

  header {
    padding-bottom: 0;
  }
}
