.servtype {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.servtype__container {
  display: flex;
  width: 100%;
  max-width: 1300px;
  justify-content: space-between;
  margin-top: 100px;
  padding-bottom: 100px;
}

.servtype__item {
  position: relative;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;

  &::before {
    content: "";
    position: absolute;
    width: 300px;
    height: 300px;
    top: -78px;
    left: 79px;
    background: #eab734;
    border-radius: 50%;
    z-index: 10;
  }
}


.servtype__item img {
  z-index: 1000;
  position: relative;
}


.servtype__item h3 {
  padding-top: 40px;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  max-width: 211px;
}

.servtype__btn {
  width: 264px;
  height: 65px;
  border-radius: 30px;
  color: black;
  background: #eab734;
  margin-top: 28px;
  border: 2px solid #EAB734;

  &:hover {
    background-color: white;
  }
}


.servtype__link {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: $fontfam;
  color: black;
  border: 2px solid #eab734;
  text-align: center;
  border-radius: 30px;
  width: 264px;
  height: 65px;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  /* identical to box height */

  &:hover {
    background-color: #eab734;
  }
}


.servtype h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  padding-top: 107px;
  padding-bottom: 52px;
}

@media (max-width: 1300px) {
  .servtype__container {
    justify-content: center;
    flex-wrap: wrap;
  }

  .servtype__item {
    &:last-child {
      margin-top: 140px;
    }
  }
}

@media (max-width: $mobile-width) {
  .servtype__item img {
    width: 213px;
    height: 127px;
  }

  .servtype__item {
    width: 280px;
    padding-bottom: 30px;

    &:last-child {
      margin-top: 0;
    }

    &::before {
      width: 204px;
      height: 204px;
      top: 0;
      left: 56px;
    }

    img {
      top: 77px;
      margin: 0 auto;
    }
  }

  .servtype__container {
    max-width: 768px;
    width: 100%;
    flex-wrap: nowrap;
    margin-top: 32px;
    padding-bottom: 50px;
  }

  .servtype h2 {
    padding-bottom: 0;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    max-width: 203px;
    padding-top: 60px;
  }

  .servtype__item h3 {
    padding-top: 94px;
    margin: 0 auto;
  }

  .servtype__footer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
