.tarif__item {
  display: flex;
  width: 100%;
  max-width: $container;
  justify-content: space-between;
  padding-bottom: 84px;
}

.tarif {
  padding-top: 67px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tarif h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  padding-bottom: 90px;
}

.tarif__info-block p {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 138.69%;
  color: black;
  max-width: 753px;
  padding-bottom: 42px;
}

.tarif__text {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 138.69%;
  color: black;
  max-width: 753px;
  padding-bottom: 42px;
}

.tarif__car-imgs {
  display: flex;
  width: 380px;
  position: relative;
}

.tarif-bg {
  background-color: #EAB734;
}

.tarif__car-imgs img {
  margin-right: 20px;
  transition: 1s;

  &:hover {
    transform: scale(1.4);
  }

  &:last-child {
    margin-right: 0;
  }
}

.tarif__info-content {
  display: flex;
  width: 100%;
  max-width: 770px;
  justify-content: space-between;
}


.tarif__price-btn {
  width: 264px;
  height: 65px;
  color: white;
  background-color: black;
  border-radius: 30px;
}


.tarif__price h3 {
  font-style: normal;
  font-weight: 900;
  font-size: 50px;
  line-height: 60px;
  /* identical to box height */

  text-align: right;
  padding-bottom: 44px;
}


.tarif__car p {
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  padding-bottom: 17px;
}

.tarif__car {
  display: flex;
  width: 100%;
  max-width: 380px;
  justify-content: space-between;
  padding-bottom: 25px;
}


.tarif__car-spans {
  display: flex;
  flex-direction: column;
}


.tarif__car-spans span {
  padding-bottom: 12px;
}


.tarif__footer-wrapper {
  display: flex;
  flex-direction: column;
}


.tarif__price {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}


.tarif__img-block {
  position: relative;
  z-index: 1000;
  padding-right: 30px;

    img {
      position: relative;
      z-index: 1000;
      top: 30px;
    }

    &::before {
      content: "";
      position: absolute;
      width: 300px;
      height: 300px;
      background-color: #eab734;
      border-radius: 50%;
      z-index: 1;
      left: 56px;
    }

    h3 {
      position: relative;
      z-index: 1000;
      font-style: normal;
      font-weight: bold;
      font-size: 20px;
      line-height: 24px;
      text-align: center;
    }
}


@media (max-width: 1250px) {
  .tarif__item {
    padding-right: 10px;
    padding-left: 10px;
  }

  .tarif__img-block {
    &::before {
      left: 37px;
    }
  }
}

@media (max-width: $tablet-width) {
  .tarif__item {
    flex-direction: column;
    align-items: center;
  }

  .tarif__img-block {
    &::before {
      top: -72px;
    }
  }

  .tarif__img-block img {
    top: 0;
  }
}


@media (max-width: $mobile-width) {
  .tarif__info-block {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .tarif__info-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    padding-bottom: 52px;
  }

  .tarif__price {
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  .tarif__text {
    text-align: center;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 138.69%;
    padding: 0 42px;
  }

  .tarif__footer-wrapper {
    width: 290px;
    max-width: none;
    justify-content: space-between;
    display: flex;
  }

  .tarif__car {
    display: flex;
    justify-content: space-between;
    max-width: none;
    width: 290px;
  }

  .tarif__car-parag {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .tarif__img-block {
    padding-bottom: 22px;
    padding-right: 0;

    &::before {
      width: 119px;
      height: 119px;
      top: -32px;
      left: 15px;
    }

    img {
      width: 125px;
      height: 74px;
    }

    h3 {
      top: -15px;
    }
  }

  .tarif h2 {
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
  }

  .tarif__car-imgs {
    display: flex;
    justify-content: center;
    padding-bottom: 24px;
    width: 320px;

    img {
      width: 55px;
      height: 55px;
    }
  }

  .tarif__item {
    width: 100%;
    max-width: 320px;

    &:last-child {
      padding-bottom: 50px;
    }
  }
}
