.about {
  display: flex;
  justify-content: center;
  background-color: #eab734;
}

.about__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 59px;
  padding-bottom: 76px;
}

.about__container h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  /* identical to box height */
  padding-bottom: 38px;
  text-align: center;
}

.about__container p {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  color: black;
  text-align: center;

  &:last-child {
    text-align: center;
    max-width: 705px;
  }
}

@media (max-width: $mobile-width) {
  .about__container p {
    padding: 0 28px;
    padding-bottom: 17px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
  }

  .about__container {
    padding-bottom: 30px;
    padding-top: 48px;
  }

  #map__about {
    height: 277px;
    width: 100%;
  }

  .about h2 {
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    line-height: 24px;
    /* identical to box height */
    padding-bottom: 17px;
    text-align: center;
  }
}
